  align-items: flex-start;
}
.back-to-top {
  width: 188px;
  flex-direction: column;
  justify-content: flex-start;
  gap: 7px;
}
.footer-content {
  width: 350px;
  flex-direction: row;
  justify-content: center;
  padding: 0 0 0 2px;
  box-sizing: border-box;
}
.single-social-icon,
.social-icon-set-one {
  width: 30px;
  position: relative;
  cursor: pointer;
  z-index: 1;
}
.social-icon-set-one {
  height: 30px;
  min-height: 30px;
}
.single-social-icon {
  height: 28px;
}
.social-icon-set-two {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 1px 0 0;
}
.footer-icons-container,
.social-icons1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.social-icons1 {
  flex: 1;
  justify-content: space-between;
  gap: 20px;
}
.footer-icons-container {
  align-self: stretch;
  justify-content: flex-start;
  padding: 0 72px 0 74px;
}
.jernes-andriy-all-container {
  position: relative;
  z-index: 1;
}
.footer-icons {
  width: 350px;
  align-items: flex-start;
  gap: 43.7px;
  text-align: left;
  font-size: 18px;
  font-family: Nunito;
}
.footer,
.footer-icons,
.jernes {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.footer {
  align-self: stretch;
  background-color: #1a1a1a;
  overflow-x: auto;
  flex-shrink: 0;
  align-items: center;
  padding: 69px 22px 69px 20px;
  gap: 51px;
  text-align: center;
  font-size: 15px;
  color: #fff;
  font-family: Montserrat;
}
.jernes {
  width: 100%;
  position: relative;
  background-color: #fff;
  overflow-y: auto;
  align-items: flex-start;
  line-height: normal;
  letter-spacing: normal;
}
